.advantages {
    background-color: var(--main-green-color);
    padding-bottom: 36px;
    padding-top: 42px;

    @media screen and (min-width: 768px) {
        padding-bottom: 40px;
    }

    @media screen and (min-width: 768px) {
        padding-bottom: 86px;
    }
}

.advantages__container {
    padding-left: 45px;
    padding-right: 45px;

    @media screen and (min-width: 480px) {
        width: 480px;
        margin-left: auto;
        margin-right: auto;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-left: 76px;
        padding-right: 76px;
        margin-left: auto;
        margin-right: auto;
    }

    @media screen and (min-width: 1200px) {
        width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
        margin-left: auto;
        margin-right: auto;
    }
}

.advantages__list {
    display: flex;
    flex-direction: column;
    gap: 25px;

    @media screen and (min-width: 768px) {
        flex-direction: row;
        gap: 80px;
    }

    @media screen and (min-width: 1200px) {
        gap: 32px;
    }
}

.advantages__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.advantages__item::before {
    content: "";
    display: inline-block;
    width: 50px;
    height: 50px;
    background-color: transparent;
    margin-bottom: 16px;
    background-size: cover;

    @media screen and (min-width: 768px) {
        margin-bottom: 32px;
    }

    @media screen and (min-width: 768px) {
        margin-bottom: 40px;
    }
}

.advantages__item:nth-child(1)::before {
    background-image: url(../images/mobile/mob-advantages-pict-1-1x.png);

    @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../images/mobile/mob-advantages-pict-1-2x.png);
    }
}

.advantages__item:nth-child(2)::before {
    background-image: url(../images/mobile/mob-advantages-pict-2-1x.png);

    @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../images/mobile/mob-advantages-pict-2-2x.png);
    }
}

.advantages__item:nth-child(3)::before {
    background-image: url(../images/mobile/mob-advantages-pict-3-1x.png);

    @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../images/mobile/mob-advantages-pict-3-2x.png);
    }
}


.advantages__numbers {
    font-size: 36px;
    font-weight: 700;
    line-height: calc(42 / 36);
    text-transform: uppercase;
    margin-bottom: 18px;

    @media screen and (min-width:768px) {
        margin-bottom: 16px;
        font-size: 48px;
        line-height: calc(59 / 48);
    }

    @media screen and (min-width:768px) {
        font-size: 58px;
        line-height: calc(71 / 58);
    }
}


.advantages__text {
    text-align: center;
    font-size: 12px;
    font-weight: 700;
    line-height: calc(20 / 12);

    @media screen and (min-width:768px) {
        font-size: 14px;
        font-weight: 400;
        line-height: calc(24 / 14);
    }

    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: calc(28 / 16);
        letter-spacing: 0.02em;
    }
}